	export default {
		data() {
			return {
				 swiperItems : [],
				 
				  tabCurrentIndex: 0,
            swiperCurrentIndex: 0,
            tabs: [
                { name: '热门话题', id: 'guanzhu' },
                { name: '话题榜', id: 'tuijian' },
                { name: '超话社区', id: 'tiyu' },
                
